<script>
  import Vue from 'vue'
  import api from 'src/packages/Api'
  import Loading from 'partials/Loading'
  import Relateds from 'view/components/relateds'
  import VueTagsInput from '@johmun/vue-tags-input'

  export default {
    name: 'update-question',
    data () {
      return {
        question: {},
        title: '',
        question_input: '',
        answer: '',
        showhomepage:0,
        id: '',
        category_id: [],
        categoriesItems: [],
        question_type:[],
        question_typeItem:'',

        related: [],

        loaded: false
      }
    },
    components: {
      VueTagsInput,
      Loading,
      Relateds
    },
    async created () {
      try {
        const response = await api.get(`questions/question_get/${this.$route.params.id}`)
        const question_type = await api.get('questions/question_type')        
        const questions = api.get('questions')

        const res = response.data

        const related = await api.post('related', res.relateds.data)

        this.question = res
        this.id = res.id
        this.title = res.title  
        this.question_input = res.question
        //this.categoriesItems = res.category_id

        const res1 = res.category_id
        for (let t in res1) {
          this.categoriesItems.push({
            id:parseInt(res1[t].id),
            title:res1[t].title
          })
        }

        this.question_typeItem = res.question_type
        setTimeout(() => {
          this.$refs.editor.setContent(res.answer)
        }, 500)        

        this.related = related.data.map(r => {
          const selector = r.type === 'categories' ? 'name' : 'title'

          const rel = {
            selector,
            id: r.entity,
            type: {id: r.type, name: this.$t(`related.type_${r.type}`)}
          }

          return rel
        })

        const qtres = question_type

        for (let t in qtres) {
          this.question_type.push({
            id:t,
            title:qtres[t]
          })
        }

        const qres = questions.data
        for (let t in qres) {
          this.category_id.push({
            id:qres[t].id,
            title:qres[t].title
          })
        }

        this.loaded = true
      } catch(error) {  
        console.log(error)
        this.$store.dispatch('addToastMessage',
          { text: this.$t(`errors.${error.data.status_code}`) }
        )
        this.$router.push({ name: 'categories' })
      }
    },
    methods: {
      changeRelated ({old, changed}) {
        const relates = this.related.map(r => {
          if (r === old) {
            return changed
          } else {
            return r
          }
        })

        this.related = relates
      },

      deleteRelated (rel) {
        this.related = this.related.filter(r => r !== rel)
      },
      update () {
        if (!this.question_typeItem) {
          this.$store.dispatch('addToastMessage', { text: Vue.i18n.translate('errors.category') })
        } else {
          this.$validator.validateAll().then((result) => {
            const relations = this.related
              .filter(r => !_.isEmpty(r.id))
              .map(r => {
                return {
                  type: r.type.id,
                  related_id: r.id.id
                }
              })

            if (result) {
              const data = {
                title: this.title,
                question: this.question_input,
                answer: this.$refs.editor.getContent(),                
                home_show: this.showhomepage,
                category_id: this.categoriesItems,
                question_type: this.question_typeItem.id,
                related: relations
              }
              
              api.put(`questions/${this.id}`, data).then(response => {
                this.$store.dispatch('addToastMessage',
                  { text: Vue.i18n.translate('question.updated'), type: 'success' }
                )
                this.$router.push({ name: 'question', params: { id: response.data.id } })
              }).catch(error => {
                this.$store.dispatch('addToastMessage',
                  { text: Vue.i18n.translate('errors.any'), error }
                )
              })
            }
          })
        }
      }
    },
  }
</script>

<template>
  <loading v-if="!loaded"/>
  <div class="form-elements" v-else>
    <div class="row">
      <div class="col-md-12">
        <vuestic-widget :headerText="'menu.questions.update' | translate">
          <div class="row">
            <div class="col">
              <div class="row row-reverse">
                <div class="col">
                  <div class="form-group with-icon-right"
                       :class="{'has-error': errors.has('title')}">
                    <div class="input-group">
                      <input
                        id="title"
                        name="title"
                        v-model="title"
                        v-validate="'required'"
                        required/>
                      <i
                        class="fa fa-exclamation-triangle icon-right input-icon"
                        v-show="errors.has('title')"></i>
                      <label class="control-label" for="title">{{'question.title'
                        | translate}}</label><i class="bar"></i>
                      <small v-show="errors.has('title')"
                             class="help text-danger">{{
                        errors.first('title')
                        }}
                      </small>
                    </div>
                  </div>
                </div>

                <div class="col">
                  <vuestic-simple-select
                    :label="'question.question_type' | translate"
                    v-validate="'required'"
                    v-model="question_typeItem"
                    option-key="title"
                    v-bind:options="this.question_type">
                  </vuestic-simple-select>
                </div>     
                <div class="col">
                  <vuestic-multi-select
                    option-key="title"
                    v-bind:options="this.category_id"
                    v-model="categoriesItems"
                    :label="'question.questions' | translate">
                    </vuestic-multi-select>
                </div>                           
              </div>

              <div class="row row-reverse mb-4">
                <div class="col-md-8 text-right">
                  <div class="form-group with-icon-right"
                     :class="{'has-error': errors.has('title')}">              
                    <div class="input-group">
                      <input
                        id="question_input"
                        name="question_input"
                        v-model="question_input"
                        v-validate="'required'"
                        required/>
                      <i
                        class="fa fa-exclamation-triangle icon-right input-icon"
                        v-show="errors.has('title')"></i>
                      <label class="control-label" for="question">{{'question.question'
                        | translate}}</label><i class="bar"></i>
                      <small v-show="errors.has('question')"
                             class="help text-danger">{{
                        errors.first('question')
                        }}
                      </small>
                    </div>
                  </div>
                </div>
              </div>

              <div class="row">
                <div class="col">
                  <Vueditor ref="editor" style="height: 500px;"/>
                </div>
              </div>
            </div>            
          </div>

          <relateds :relateds="related" @delRel="deleteRelated" @changeRel="changeRelated"/>

          <div class="row mt-5">
            <div class="col col text-center">
              <button class="btn btn-success"  @click="update">
                {{'buttons.update' | translate}}
              </button>
            </div>
          </div>
        </vuestic-widget>        
      </div>
    </div>
  </div>
</template>
